export function request (options){
	uni.request({
		url: 'http://192.168.1.136:8769/'+options.url,
		method: "POST",
		dataType: "json",
		header: {
			"content-type": "application/x-www-form-urlencoded"
		},
		data: options.data,
		success: (res) => {
			console.log('request success', res)
			if(res.data=='noLogin'){
				// login()
// 				console.log('request noLogin')
// 				window.location.href = res.data.data.code_url
// 				return
			}
			if (res.statusCode != 200) {
				uni.showModal({
					content: res.statusCode + ' 服务器请求错误',
					showCancel: false
				});
				return
			}
			if (res.data.errcode > 0) {
				if (typeof options.success == "function") {
					options.success(res.data);
				}
			} else {
				if (res.data.errmsg=="用户被禁用") {
					this.showMsgModal({ content: '用户已被禁用！请联系客服' });
					
					return;
				}
				this.showMsgModal({ content: res.data.errmsg || '服务器返回错误！' });
				return;
			}
		},
		fail: (err) => {
			console.error('request fail', err);
			if (typeof options.fail == "function") {
				options.fail(res);
			} else if (typeof options.fail == "string") { //请求失败的弹框提示
				uni.showModal({
					content: options.fail,
					showCancel: false
				});
				
			} else{
				uni.showModal({
					content: err.errMsg,
					showCancel: false
				});
			}
		},
		complete:() => {
			console.log('request complete');
			if (typeof options.complete == "function") {
				options.complete();
			}
		}
	});
}

